import React, { useState, useEffect } from 'react';
import Menu from '../utils/menu';
import logo from '../../assets/images/logo/logo.png';

const Header = ()  => {
    const [scrolled, setScrolled] = useState(false);    
    useEffect(() => {               
        const handleScroll = () => {
            const isScrolled = window.scrollY > 300;
            if (isScrolled !== scrolled) {
                setScrolled(!scrolled);
            }
        };    
        document.addEventListener('scroll', handleScroll, { passive: true });    
        return () => {
            document.removeEventListener('scroll', handleScroll);
        };
    }, [scrolled]);

    var mobile = false;

    if(typeof window !== 'undefined')
    {
        var match = window.matchMedia("(max-width: 991px)");
        mobile = match.matches;
        match.addListener(() => {
            mobile = match.matches;
        });     

        const [isMobileBtnActive, setMobileBtnActive] = useState(false);
        const onMobileBtnClick = () => {
            setMobileBtnActive(!isMobileBtnActive);
        };

        const desktopMenu = mobile ? null : <nav id="main-nav" className="main-nav"><Menu /></nav>
        const mobileMenu = mobile ? <nav id="main-nav-mobi" className={isMobileBtnActive ? 'main-nav' : 'main-nav hide'}><Menu /></nav> : null;

        return (
            <header id="header_main" className={scrolled ? 'header is-fixed is-small' : 'header'}>
                <div className="container">
                    <div id="site-header-inner">
                        <div className="header__logo">
                            <a href="index.html">
                                <img src={logo} alt="NON FUNGIBLE CUP" />
                                <span className="h7">NON FUNGIBLE CUP</span>
                            </a>
                        </div>
                        {desktopMenu}
                        <a className="tf-button btn-effect d-none d-sm-block" href="#">
                            <span className="boder-fade"></span>
                            <span className="effect">join discord</span>
                        </a>
                        <div className={isMobileBtnActive ? 'mobile-button active' : 'mobile-button'} onClick={onMobileBtnClick}><span></span></div>
                    </div>
                </div>
                {mobileMenu}
            </header>
        );

    }
    else   return (<br />);

    
};

export default Header;